body {
  margin: 0;
  padding: 0;
  background-color: #191919;
}

.header {
  height: 70px;
}
.head-title {
  margin: 30px;
  margin-top: 25px;
  margin-bottom: 0;
  font-size: 40px;
  color: #e50914;
  letter-spacing: -3px;
}

.container {
  margin-left: 2%;
  margin-right: 2%;
}
.main {
  padding: 10px;
}
.genre {
  color: #ffff;
  font-size: 30px;
  font-weight: 400;
  font-family: sans-serif;
  margin-left: 30px;
  display: inline-block;
  margin-top: 10px;
}
.show-all {
  transition: 0.8s;
  color: #54b9c5;
  font-size: 18px;
  margin-left: 10px;
  text-decoration: none;
}
@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur(0);
  }
}
.show-all:hover .show-all-hid {
  display: inline-block;
  animation: fade-in 0.2s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
}
.show-all-hid {
  transition: 0.5s;
  display: none;
  opacity: 0;
}
.sort-select {
  -webkit-appearance: none; /* 네이티브 외형 감추기 */
  -moz-appearance: none;
  appearance: none;
  width: fit-content;
  background-color: #191919;
  border: 0;
  padding: 0.5em;
  border-radius: 0px;
  color: rgb(172, 172, 172);
  vertical-align: middle;
  font-size: 12px;
  cursor: pointer;
}
.sort-select:hover {
  color: rgb(203, 203, 203);
}
.genre-div {
  width: 100%;
  height: fit-content;
  max-height: 250px;
  overflow-y: hidden;
  overflow-x: scroll;
  white-space: nowrap;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.genre-div::-webkit-scrollbar {
  display: none;
}
.rank {
  display: inline-block;
  width: 300px;
  height: 250px;
  overflow-y: hidden;
  margin-left: -30px;
}
.rank:first-child {
  margin-left: -60px;
}
.rank-num {
  height: 200px;
  vertical-align: bottom;
  margin-left: 27px;
  margin-top: 60px;
  position: relative;
  z-index: -1;
}
.rank-poster {
  vertical-align: bottom;
  height: 220px;
  width: 150px;
  float: right;
  margin-left: -200px;
  overflow-y: hidden;
}
.rank-poster:hover {
  transform: scale(1.01);
}
